<template>
  <div>
    <h4>Applications</h4>

  <b-row>

  <div v-for="app in applications">
    <b-card>
      {{app.Status}}
      {{app.ApplicantId}}
      <b-btn @click="applicant = app.Applicant">View</b-btn>
    </b-card>
  </div>
  </b-row>
    <detail :applicant="applicant"></detail>
  </div>
</template>

<script>
    import Detail from './detail'
    export default {
      name: '',
      props: ['offer'],
      data () {
        return {
          applications: [],
          applicant: ''
        }
      },
      mounted () {
        this.$store.dispatch('$_supplier_view/GET_OFFER_APPLICATIONS', 1).then(data => {
          this.applications = data
        })
      },
      components: {Detail}
    }
</script>

<style scoped>

</style>



