<template>
    <div v-if="applicant">
      <b-row>
        <b-col>
          <b-card>
      <h1>{{applicant.FName}} {{applicant.MName}}</h1>

      <score-card :score_history="applicant.Scores.map(s => s.Score)"></score-card>
<b-row align-h="end">
          <b-button-group>
            <b-button size="lg" variant="success">Accept</b-button>
            <b-button size="lg" variant="danger">Decline</b-button>
          </b-button-group>
</b-row></b-card>
        </b-col>
        <b-col>
          <b-card>
          <h2>Attributes</h2>
            <h5>IncomeFactor</h5>
            <b-progress :value="56" :max="100" show-value animated></b-progress>
            <h5>Familly Factor</h5>
            <b-progress :value="65" :max="100" show-value animated></b-progress>

          </b-card>
        </b-col>
      </b-row>
    </div>
</template>

<script>
    import ScoreCard from '../../../../../components/ScoreCard'
    export default {
      name: '',
      props: ['applicant'],
      components: {ScoreCard},
      data () {
        return {
          // score_history: this.applicant.Scores.map(s => s.Score)
        }
      }
    }
</script>

<style scoped>

</style>



